import Layout from "../components/layout";
import axios from "axios";
import Error from "./_error";

const About = ( {data , status} ) => {

    console.log(status)

    if (status >= 200 && status <= 300 ){
        return <Error />
    }
    return (
        <Layout title="About">
            <p> Hi I'm mohamed And I'm a web developer </p>
            <p> github account : <a href={data.url}>Mohamed </a></p>
            <p> {data.bio} </p>
            <img className="img" src={data.avatar_url} alt="image"/>
            <style jsx>{`
            .img {
                width : 300px;
                margin-bottom : 2rem;
            }
            `}</style>
        </Layout>
    )
}

About.getInitialProps = async () => {
    const res = await axios("https://api.github.com/users/bsasterboy32");

    return { data : res.data , status : res.status }
}

export default About